<template>
  <div class="item-choose" @click="log">
    <img :src="url" :alt="alt">
    <h3 class="title3">{{title}}</h3>
    <p>{{desc}}</p>
  </div>
</template>

<script>
  export default {
    name: 'AppChoose',
    props: {
      title: String,
      desc: String,
      img: { type: String, default: 'logo.png' },
      alt: { type: String, default: '' },
      id: String
    },
    computed: {
      url: function () {
        return require('../assets/' + this.img)
      }
    },
    methods: {
      log: function () {
        console.log(this.id)
      }
    }
  }
</script>

<style scoped lang="scss">
  .item-choose {
    flex-basis: 240px;
    text-align: center;
    @media (min-width: $phoneWidth) and (max-width: $tableWidth) {
      flex-basis: 30%;
    }
    @media screen and (max-width: $phoneWidth) {
      margin-bottom: 40px;
    }

    img {
      width: 90%;
    }

    h3.title3 {
      margin-top: 70px;
      @media screen and (max-width: $desktopWidth) {
        margin-top: 40px;
      }
      @media screen and (max-width: $phoneWidth) {
        margin-top: 20px;
      }
    }

    p {
      line-height: rem(27px);
    }
  }
  .item-choose:last-child {
    margin-bottom: 0;
  }
</style>
