<script>
  import AppButton from './AppButton'
  import AppChoose from './AppChoose'
  import AppFooter from './AppFooter'

  export default {
    name: 'AppSection',
    components: {
      AppButton,
      AppChoose,
      AppFooter
    },
    data () {
      return {
        choose: [
          {
            url: 'lifetime.png',
            title: 'Lifetime Updates',
            desc: 'Free lifetime updates for our lates collection.'
          },
          {
            url: 'mobil.png',
            title: 'Anywhere Videos',
            desc: 'Access world-class online yoga videos can be seen on the applic'
          },
          {
            url: 'access.png',
            title: 'Access Unlimiteds',
            desc: 'Find lots of yoga video tutorials, from all over the world'
          }
        ]
      }
    },
    computed: {
      random: function (array) {
        let min = 0
        let max = 50
        let rand = min + Math.random() * (max + 1 - min)
        rand = Math.floor(rand)
        return array.push(rand)
      }
    }
  }
</script>

<style lang="scss">
